@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Open+Sans&display=swap);
body {
  background-color: #BDA4B1;
  color: #E7EBE6;
  text-align: center;
  margin: 0px;
}

.App {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: auto;
}

#title {
    font-family: 'Indie Flower', cursive;
    align-self: center;
    font-size: 8vh;
}

#clock {
    font-family: 'Open Sans', sans-serif;
    margin-top: 10px;
    margin-bottom: 10px;
}

#length-control {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

#break {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

#session {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.timer {
    margin-top: 20px;
    margin-bottom: 20px;
}

#arrowNumArrow {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

#arrowNumArrow button {
    background-color: #BDA4B1;
    color: #E7EBE6;
    outline: none;
    border: none;
    cursor: pointer;
}

.length-control {
    width: 27vh;
    font-size: 3vh;
}


#timer {
    border: 5px solid #996881;
    border-radius: 23px;
    width: 250px;
    font-size: 4vh;
    display: inline-block;
    margin-top: 50px;
    margin-bottom: 40px;
}

#timer-label {
    margin: 10px;
}

#time-left {
    font-size: 5vh;
    margin: 10px;
}

.timer-wrapper {
    margin: 5px;
}

.timer-control button {
    background-color: #BDA4B1;
    color: #E7EBE6;
    outline: none;
    border: none;
    cursor: pointer;
}

.arrow {
    margin: -5px;
}

.timer-control {
    font-size: 4vh;
}

